import { Container, Row, Col, Tab, } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import projectsDetails from "../utils/projectsDetails";

import 'animate.css';
import TrackVisibility from 'react-on-screen';

export const Projects = () => {

  

  return (
    <section className="project" id="project">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isvisible }) =>
              <div className={isvisible ? "animate__animated animate__fadeIn": ""}>
                <h2>Project's</h2>
                <p style={{textAlign: 'justify'}}>Welcome to my portfolio! Here you will find a showcase of some of the exciting and innovative projects I have worked on. Each project represents a unique journey, fueled by my passion for creativity, problem-solving, and technology. I am thrilled to share these endeavors with you, highlighting the skills and expertise I have gained along the way.</p>
                <Tab.Container id="projects-tabs" defaultActiveKey="first">
                  
                  <Tab.Content id="slideInUp" className={isvisible ? "animate__animated animate__slideInUp" : ""}>
                    <Tab.Pane eventKey="first">
                      <Row className="justify-content-start">
                        {
                          projectsDetails.map((project, index) => {
                            return (
                              <ProjectCard
                                key={index}
                                {...project}
                                />
                            )
                          })
                        }
                      </Row>
                    </Tab.Pane>
                    <Tab.Pane eventKey="section">
                      {/* <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Cumque quam, quod neque provident velit, rem explicabo excepturi id illo molestiae blanditiis, eligendi dicta officiis asperiores delectus quasi inventore debitis quo.</p> */}
                    </Tab.Pane>
                    <Tab.Pane eventKey="third">
                      {/* <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Cumque quam, quod neque provident velit, rem explicabo excepturi id illo molestiae blanditiis, eligendi dicta officiis asperiores delectus quasi inventore debitis quo.</p> */}
                    </Tab.Pane>
                  </Tab.Content>
                </Tab.Container>
              </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
    </section>
  )
}
