import { Container, Row, Col } from "react-bootstrap";


import navIcon1 from "../assets/img/nav-icon1.svg";
import navIcon2 from "../assets/img/nav-icon2.svg";
import navIcon3 from "../assets/img/nav-icon3.svg";

export const Footer = () => {
  return (
    <footer className="footer">
      <Container>
        <Row className="align-items-center">
          <Col size={12} sm={6}></Col>
          <Col size={12} sm={6} className="text-center text-sm-end">
            <div className="social-icon">
              <a href="https://in.linkedin.com/in/gurrudev"><img src={navIcon1} alt="Icon" /></a>
              <a href="https://github.com/gurrudevs"><img src={navIcon2} alt="Icon" /></a>
              <a href="https://instagram.com/gurrudev"><img src={navIcon3} alt="Icon" /></a>
            </div>
            <p>&copy; Copyright  2021. All Rights Reserved</p>
          </Col>
        </Row>
      </Container>
    </footer>
  )
}
